<template>
  <div class="tree-config">
    <Form :label-width="120">
      <div class="config"
           v-for="(item, index) in currentValue" :key="index">
        <!-- 别名 -->
        <Form-item :label="item.name"
                   v-if="item.code === 'alias' && cType !== 1">
          <Input v-model="item.configData.configData"></Input>
        </Form-item>
        <!-- 显示类型 -->
        <Form-item :label="item.name"
                   v-if="item.code === 'showType' && cType === 1">
          <Select filterable="filterable"
                :size="'default'"
                :transfer="false"
                v-model="item.configData.configData">
            <Option v-for="(com, index) in typeList"
                    :key="index"
                    :value="com.id">{{ com.name }}</Option>
          </Select>
          <!-- {{ item.configData }} -->
        </Form-item>
        <!-- 默认值 -->
        <Form-item :label="item.name"
                   v-if="item.code === 'value'">
          <!-- <Input v-model="item.configData.configData"></Input> -->
          <!-- {{ item.configData }} -->
          <Row style="margin-top: 10px">
            <Col span="24">
            <Editor v-model="item.configData.otherConfig" :sourceType="2"/>
            </Col>
          </Row>
        </Form-item>
        <!-- 名称 -->
        <Form-item :label="item.name"
                   v-if="item.code === 'name'">
          <!-- <Input v-model="item.configData.configData"></Input> -->
          <!-- {{ item.configData }} -->
          <Row style="margin-top: 10px">
            <Col span="24">
            <Input v-model="item.configData.configData" @on-change="update"></Input>
            </Col>
          </Row>
        </Form-item>
        <!-- 同级名称1 -->
        <Form-item :label="item.name"
                   v-if="item.code === 'name1'">
          <!-- <Input v-model="item.configData.configData"></Input> -->
          <!-- {{ item.configData }} -->
          <Row style="margin-top: 10px">
            <Col span="24">
            <Input v-model="item.configData.configData" @on-change="update"></Input>
            </Col>
          </Row>
        </Form-item>
        <!-- 同级名称2 -->
        <Form-item :label="item.name"
                   v-if="item.code === 'name2'">
          <!-- <Input v-model="item.configData.configData"></Input> -->
          <!-- {{ item.configData }} -->
          <Row style="margin-top: 10px">
            <Col span="24">
            <Input v-model="item.configData.configData" @on-change="update"></Input>
            </Col>
          </Row>
        </Form-item>
        <!-- 同级名称3 -->
        <Form-item :label="item.name"
                   v-if="item.code === 'name3'">
          <!-- <Input v-model="item.configData.configData"></Input> -->
          <!-- {{ item.configData }} -->
          <Row style="margin-top: 10px">
            <Col span="24">
            <Input v-model="item.configData.configData" @on-change="update"></Input>
            </Col>
          </Row>
        </Form-item>
      </div>
    </Form>
  </div>
</template>

<script>
import Editor from '@/components/editor'

export default {
  name: 'tree-config',
  components: {
    Editor
  },
  props: {
    cType: {
      type: Number,
      require: true
    },
    configs: {
      type: Array,
      require: true
    }
  },
  watch: {
    currentValue: {
      handler (newValue, oldValue) {
        this.$emit('input', {
          value: this.currentValue
        })
      },
      deep: true
    }
  },
  data () {
    return {
      uuid: '00000000-0000-0000-0000-000000000000000000000016',
      currentValue: null,
      maxLengthN: null,
      maxLengthDefaultVal: null,
      valueN: null,
      valueDefaultVal: null,
      typeList: [
        {
          id: 'cascader',
          name: '级联选择'
        }
      ]
    }
  },
  created () {
    this.currentValue = this.deepCopy(this.configs)
    for (const n in this.currentValue) {
      if (this.currentValue[n].code === 'value') {
        this.valueN = n
        this.valueDefaultVal = Number(this.currentValue[n].configData.configData)
      } else if (this.currentValue[n].code === 'maxLength') {
        this.maxLengthN = n
        this.maxLengthDefaultVal = Number(this.currentValue[n].configData.configData)
      }
    }
  },
  methods: {
    numberChange (val, name) {
      this.currentValue[this[name + 'N']].configData.configData = val
    },
    update () {
      this.$emit('update', true)
    }
  }
}
</script>

